import { TextareaAutosize } from "@mui/material";
import React, { useCallback, useState } from "react";
import {
  InputContainer,
  Input,
  Label,
  ErrorLabel,
  HintContainer,
  HintLabel,
  Icon,
} from "./Atom.tsx";

export interface TextInputProps {
  onChange?: (value: any) => void;
  value?: any;
  style?: any;
  name?: string;
  customRef?: any;
  isFormInputElement?: boolean;
  required?: boolean;
  isEmail?: boolean;
  isLink?: boolean;
  hint?: string;
  hidden?: boolean;
  imageSrc?: any;
  label?: string;
  type?: string;
  pattern?: string;
  maxLength?: number;
  disabled?: boolean;
  expandable?: boolean;
  endText?: string;
  isNumber?: boolean;
  min?: number;
  max?: number;
}

export interface RefProps {
  getValue: () => string;
  validate: () => any;
  name: string;
}

const Regexp = {
  PhoneFormat: /(\d{0,3})(\d{0,3})(\d{0,4})/,
  Email: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,10}$/i,
  CardMask: /(\d{0,4})(\d{0,4})(\d{0,4})(\d{0,4})/,
  Number: /\D/g,
  USPhoneFormat: /([+]?\d{1,4}[.-\s]?)?[(]\d{3}[)][ ]\d{3}-\d{4}/,
  Password: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/,
};

const TextInput = (props: TextInputProps) => {
  const {
    value,
    customRef,
    name,
    required,
    isEmail,
    isLink,
    imageSrc,
    label,
    hidden,
    type = "text",
    pattern,
    maxLength,
    disabled,
    hint,
    expandable,
    endText,
    isNumber,
    onChange,
  } = props;
  const getInitValue = useCallback(() => {
    return (value || "").toString();
  }, [value]);

  const [inputValue, setValue] = useState<any>(getInitValue());
  const [count, setCount] = useState(getInitValue().length);
  const [error, setError] = useState({ error: false, message: "" });

  const validate = useCallback(
    (input: any) => {
      let newState = {
        error: false,
        message: "",
      };
      if (!input && required) {
        newState = {
          error: true,
          message: label + " is missing",
        };
      }
      if (input && isEmail && !input.toString().match(Regexp.Email)) {
        newState = {
          message: "please enter valid email",
          error: true,
        };
      }
      // if (input && isLink && !input.includes(".pdf")) {
      //   newState = {
      //     message: "please enter valid pdf link",
      //     error: true,
      //   };
      // }
      if (input && isLink) {
        var res = /^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/;
        if(!res.test(input)){
          newState = {
            message: "please enter valid document link",
            error: true,
          };
        }
      }
      setError(newState);
      return newState;
    },
    [isEmail, label, required]
  );

  const onChangeValue = (e: React.FormEvent<HTMLInputElement>) => {
    validate(e.currentTarget.value);
    setValue(e.currentTarget.value);
    onChange && onChange(e.currentTarget.value);
    setCount(e.currentTarget.value.length);
  };
  const onKeyPressEvent = (event: any) => {
    const keyCode = event.keyCode || event.which;
    const keyValue = String.fromCharCode(keyCode);
    if (!new RegExp("[0-9]").test(keyValue)) {
      event.preventDefault();
      return;
    }
  };
  React.useEffect(() => {
    customRef && customRef({ value: inputValue, validate, name: name || "" });
  }, [customRef, inputValue, name, validate]);

  React.useEffect(() => {
    setValue(value || "");
    setCount((value || "").length);
  }, [value]);

  return (
    <>
      <InputContainer style={ hidden ? {"display": "none"}: {} }>
        {imageSrc ? <Icon src={imageSrc} alt="" /> : null}
        {label ? (
          <Label>
            {required && <span className="required">* </span>} {label}:{" "}
          </Label>
        ) : null}
        {expandable ? (
          <TextareaAutosize
            id={name}
            className="autoresize-textarea"
            aria-label="minimum height"
            pattern={pattern}
            type={type}
            style={{width: "100%"}}
            // @ts-ignore: Unreachable code error
            onChange={onChangeValue}
            value={inputValue}
            maxLength={maxLength}
            disabled={disabled}
            //maxRows={8}
            required
          />
        ) : (
          <>
            <Input
              pattern={pattern}
              id={name}
              type={type}
              onChange={onChangeValue}
              value={inputValue}
              maxLength={maxLength}
              disabled={disabled}
              onKeyPress={isNumber ? onKeyPressEvent : null}
              required
            />
            <div style={{ padding: "0 0 0 10px" }}>{endText}</div>
          </>
        )}
      </InputContainer>
      <HintContainer>
        {error.error ? (
          <ErrorLabel>{error.message}</ErrorLabel>
        ) : hint ? (
          <HintLabel>{hint}</HintLabel>
        ) : (
          <div />
        )}
        {maxLength ? (
          <HintLabel>{`${count}/${maxLength} Characters Used`}</HintLabel>
        ) : (
          <div />
        )}
      </HintContainer>
    </>
  );
};

export default TextInput;
