import { ImageDetails } from "./Atom.tsx";

export interface ProgressBarProps {
  bgcolor?: string;
  progress?: number;
  height?: number;
  name?: string;
  size?: any;
}
const Progress_bar = (props: ProgressBarProps) => {
  const { bgcolor, progress, height, name, size } = props;
  const Parentdiv: any = {
    height: height,
    width: "100%",
    backgroundColor: "#00000040",
    borderRadius: 40,
  };

  const Childdiv: any = {
    height: "100%",
    width: `${progress}%`,
    backgroundColor: bgcolor,
    borderRadius: 40,
  };
  const filename: any = {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    wordBreak: "break-all",
    paddingRight: "20px",
  };
  return (
    <>
      {progress ? (
        <div style={{ margin: "15px 0 15px" }}>
          <p>uploading the file</p>
          <ImageDetails>
            {name && <span style={filename}>{name} </span>}
            {size && <span>{size * 0.001} kb</span>}
          </ImageDetails>

          <div style={Parentdiv}>
            <div style={Childdiv}>
              <ImageDetails />
            </div>
          </div>
          <ImageDetails>{`${progress}% done`}</ImageDetails>
        </div>
      ) : null}
    </>
  );
};

export default Progress_bar;
