import React, {
  Children,
  forwardRef,
  ReactNode,
  useImperativeHandle
} from "react";

export interface FormProps {
  onSubmit: (value: any, reset?: any) => void;
  children: ReactNode;
  pending?: boolean;
  style?: any;
}

export interface RefProps {
  getValue: () => string;
  validate: () => any;
  name: string;
}

export default forwardRef(({ onSubmit, children }: FormProps, ref) => {
  const refStore: any[] = [];

  const cloneChildren = (nodes: any) => {
    const store: any[] = [];

    Children.forEach(nodes, (currentNode) => {
      let newCurrentNode;
      if (currentNode && currentNode.props) {
        if (currentNode.props.isFormInputElement) {
          const props = {
            customRef: (node: any) => {
              refStore.push(node);
              if (currentNode.props.customRef) {
                currentNode.props.customRef(node);
              }
            }
          };
          newCurrentNode = React.cloneElement(currentNode, props);
        } else {
          const newChildren = cloneChildren(currentNode.props.children);
          const props = {};
          newCurrentNode = React.cloneElement(currentNode, props, newChildren);
        }
      } else {
        newCurrentNode = currentNode;
      }

      store.push(newCurrentNode);
    });

    return store;
  };

  useImperativeHandle(ref, () => ({
    handleSubmit() {        
      let invalidComponents: RefProps[] = [];
      const data: any = {};

      refStore.forEach((refItem) => {
        const value = refItem.value;
        const validation = refItem.validate(value);
        if (validation && validation.error) {
          invalidComponents.push(refItem);
        } else if (refItem.name) {
          data[refItem.name] = refItem.value;
          invalidComponents = invalidComponents.filter(
            (item) => item.name !== refItem.name
          );
        }
      });

      if (invalidComponents.length === 0) {
        if (typeof onSubmit === "function") {
          onSubmit(data);
        }
      }
    },
    reset() {
      refStore.forEach((refItem) => {
        refItem.reset && refItem.reset();
      });
    }
  }));

  return <div>{cloneChildren(children)}</div>;
});
