import React, { useCallback, useState, useRef, useEffect } from "react"
import {
  Icon,
  Label,
  HintContainer,
  ErrorLabel,
  HintLabel,
} from "../TextInput/Atom.tsx"

import {
  Delete,
  Input,
  Preview,
  PreviewImg,
  PreviewImgWrapper,
  PreviewImgContainer,
  FileUploadContainer,
  DragDropText,
} from "./Atom.tsx"
import Upload from "../../assets/upload.png"
import ProgressBar from "./ProgressBar.tsx"
import { Row, Column as Col } from "../StyledComponent/index.tsx"

export interface PicUploadProps {
  onChange?: any
  value?: any
  style?: any
  name?: string
  customRef?: any
  isFormInputElement?: boolean
  required?: boolean
  hint?: string
  imageSrc?: any
  label?: string
  acceptedFormat: string
  deleteFile?: any
  progress?: any
  productVariantIndex?: any
}

export interface RefProps {
  getValue: () => string
  validate: () => any
  name: string
}

const PicUpload = (props: PicUploadProps) => {
  const {
    onChange,
    value,
    customRef,
    name,
    required,
    hint,
    imageSrc,
    label,
    acceptedFormat,
    deleteFile,
    progress,
    productVariantIndex
  } = props
  const getInitValue = useCallback(() => {
    return (value || "").toString()
  }, [value])

  let uploadRef = useRef<any>()
  const [inputValue, setValue] = useState<any>(getInitValue())
  const [preview, setPreview] = useState<any>(getInitValue())
  const [error, setError] = useState({ error: false, message: "" })
  const documentName = preview?.split("/")

  const validate = useCallback(
    (input: any) => {
      let newState = {
        error: false,
        message: "",
      }
      if (!input && required) {
        newState = {
          error: true,
          message: label + " is missing",
        }
      }
      setError(newState)
      return newState
    },
    [label, required]
  )

  const onChangeValue = (e: React.FormEvent<HTMLInputElement>) => {
    let newState = {
      error: false,
      message: "",
    }
    const file: any = e.currentTarget.files && e.currentTarget.files[0]
    if (
      acceptedFormat.includes("image/") &&( file.type === "image/png" ||
      file.type === "image/jpeg" || file.type === "image/jpg")
    ) {
      const url = URL.createObjectURL(file)
      setValue(file)
      setPreview(url)
      if(typeof productVariantIndex != 'undefined')
      onChange && onChange(file,productVariantIndex)
      else 
      onChange && onChange(file)
    } else if (acceptedFormat === ".pdf" && file.type === "application/pdf") {
      const url = URL.createObjectURL(file)
      setValue(file)
      setPreview(url)
      if(typeof productVariantIndex != 'undefined')
      onChange && onChange(file,productVariantIndex)
      else 
      onChange && onChange(file)
    } else {
      newState = {
        error: true,
        message: `File type should be ${acceptedFormat} format only`,
      }
    }
    setError(newState)
  }

  const removeValue = () => {
    setValue(null)
    setPreview("")
    if(typeof productVariantIndex != undefined)
    deleteFile(productVariantIndex)
    else 
    deleteFile()
  }

  useEffect(() => {
    customRef({ value: inputValue, validate, name: name || "" })
  }, [customRef, inputValue, name, validate])
  useEffect(() => {
    setValue(value || "")
    setPreview(value || "")
  }, [value])
  return (
    <>
      {imageSrc ? <Icon src={imageSrc} alt="" /> : null}
      {label ? (
        <Label>
          {required && <span className="required">* </span>} {label}:{" "}
        </Label>
      ) : null}

      <Row>
        {!inputValue && (
          <>
            <Col lg={4}>
              <FileUploadContainer onClick={() => uploadRef.current?.click()}>
                <Input src={Upload} alt="" />
                <DragDropText>Click here to upload</DragDropText>
              </FileUploadContainer>{" "}
            </Col>
            <input
              type="file"
              accept={acceptedFormat}
              style={{ display: "none" }}
              ref={uploadRef}
              onChange={onChangeValue}
            />{" "}
          </>
        )}

        {progress > 0 && progress < 100 ? (
          <Col lg={6}>
            <ProgressBar
              bgcolor="#306297"
              progress={progress}
              height={5}
              size={inputValue?.size}
              name={inputValue?.name}
            />
          </Col>
        ) : progress == -1 ? (
          <span style={{color: "red"}}>
            Could not upload Image. Something went wrong.
          </span>
       ): null}
      </Row>

      {inputValue ? (
        <div>
          <Preview>Preview</Preview>
          <PreviewImgContainer>
            {acceptedFormat !== ".pdf" && (
              <PreviewImgWrapper>
                <PreviewImg src={preview} alt="" />
              </PreviewImgWrapper>
            )}
            {acceptedFormat === ".pdf" && (
              <a href={preview} target="_blank" rel="noreferrer">
                {documentName?.[documentName?.length - 1]}
              </a>
            )}
            <Delete onClick={removeValue}>Delete</Delete>
          </PreviewImgContainer>
        </div>
      ) : null}
      <HintContainer>
        {error.error ? <ErrorLabel>{error.message}</ErrorLabel> : <div />}
        {hint ? <HintLabel>{hint}</HintLabel> : <div />}
      </HintContainer>
    </>
  )
}

export default PicUpload
